<template>
  <div class="inspectionreportlist">
    <InspectionreportPrintOption ref="enterEdit"></InspectionreportPrintOption>

    <v-card class="mt-2">
      <v-card-title>
        <v-row>
          <v-col cols="12" md="8">
            <h2 class="subheading grey--text">
              <v-icon class="mx-1">fas fa-file-alt</v-icon>
              <span>
                {{this.$store.state.uData.p_code}} /
                {{this.$store.state.uData.name}}
              </span>
              <span>檢驗報告單</span>
            </h2>
          </v-col>
          <v-col cols="12" md="4">
            <!-- 功能表 -->
            <v-toolbar dense>
              <v-text-field
                hide-details
                prepend-icon="mdi-magnify"
                single-line
                label="搜尋"
                v-model="serachKey"
              ></v-text-field>
              <popupadd @getAllData="getAllData" ref="editProcess"></popupadd>

              <v-btn class="primary mx-1" @click="print">
                <v-icon class="mx-1">mdi-printer</v-icon>列印
              </v-btn>
            </v-toolbar>
          </v-col>
        </v-row>
      </v-card-title>

      <v-card-text>
        <v-card class="mt-2">
          <v-card-text>
            <!-- 表單內容 -->
            <v-data-iterator
              :items="searchfilter"
              :items-per-page.sync="itemsPerPage"
              :footer-props="{ itemsPerPageOptions }"
            >
              <template v-slot:default="props">
                <v-simple-table fixed-header class="mt-2 text-no-wrap">
                  <template v-slot:default>
                    <thead class="title">
                      <tr>
                        <th></th>
                        <th>日期</th>
                        <th>地點</th>
                        <th>紀錄人</th>
                        <th>修改紀錄</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item,index) in props.items" :key="index">
                        <td>
                          <v-menu transition="scale-transition" offset-y>
                            <template v-slot:activator="{ on }">
                              <v-btn color="primary" dark v-on="on">功能</v-btn>
                            </template>
                            <v-list>
                              <v-list-item @click="enterEdit(item)">
                                <v-list-item-title>修改</v-list-item-title>
                              </v-list-item>
                              <v-list-item @click="enterDel(item)">
                                <v-list-item-title>刪除</v-list-item-title>
                              </v-list-item>
                              <!-- <v-list-item @click="enterNursingRecord(item)">
                                    <v-list-item-title>轉入護理紀錄</v-list-item-title>
                              </v-list-item>-->
                            </v-list>
                          </v-menu>
                        </td>
                        <td>{{item.date}}</td>
                        <td>{{item.location}}</td>
                        <td>{{item.create_man}}</td>
                        <td>{{item.edit_man}}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </template>
            </v-data-iterator>
          </v-card-text>
        </v-card>
      </v-card-text>
    </v-card>

    <!-- <pre>    {{items}}    </pre>  -->
  </div>
</template>

<script>
import qs from "qs";
import popupadd from "@/views/document/InspectionreportAdd";
import InspectionreportPrintOption from "@/components/InspectionreportPrintOption"; //列印前的內容設定
import dayjs from "dayjs";

export default {
  components: { popupadd, InspectionreportPrintOption },
  data() {
    return {
      items: [], //活動參與資料
      serachKey: "", //搜尋欄
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,
      uData: "",
    };
  },

  computed: {
    searchfilter() {
      var keys = this.serachKey.split(" ");
      var str = "";

      return keys.reduce(function (prev, element) {
        return prev.filter((item) => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.items);
    },
  },

  mounted() {
    this.auth = this.$store.state.pData; //各畫面權限暫存
    this.getAllData();
  },

  methods: {
    //取得病歷的管路使用紀錄
    getAllData() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey,
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" +
            this.$store.state.databaseName +
            "/inspectionreport",
          qs.stringify(postData)
        )
        .then((rs) => {
          // console.log(rs.data);
          this.items = rs.data.map((i) => {
            var newItem = JSON.parse(i.datalist);
            newItem["snkey"] = i.snkey;
            newItem["u_snkey"] = i.u_snkey;
            newItem["create_man"] = i.create_man;
            newItem["edit_man"] = i.edit_man;
            return newItem;
          });
          // this.$store.commit("setLoadingOff"); //讀取畫面off
          this.sortBy();
        });
    },
    sortBy() {
      //依排序
      this.items = this.items.sort(function (a, b) {
        return a.date < b.date ? 1 : -1;
      });
      // this.sort = !this.sort;
    },
    enterEdit(item) {
      //進入修改作業 -> 顯示修改頁面
      this.$refs.editProcess.$emit("childFn", item);
    },
    enterDel(delData) {
      //刪除功能
      this.$confirm("確認刪除?").then((res) => {
        if (res) {
          delData.delman =
            this.$store.state.pData.username +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")";
          var postData = {
            snkey: delData.snkey,
            tablename: "inspectionreport",
            info: JSON.stringify(delData),
          };

          this.axios
            .post(
              "general/delv3/" +
                this.$store.state.databaseName +
                "/inspectionreport",
              qs.stringify(postData)
            )
            .then((rs) => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
              }
            });
        }
      });
    },
    enterNursingRecord(item) {
      var NursingRecordData = {
        date: item.date,
        u_snkey: item.u_snkey,
        nursingrecord_content: "[檢驗報告內容]" + "\n",
      };

      if (item.location != "") {
        NursingRecordData.nursingrecord_content +=
          "檢驗地點:" + item.location + "; ";
      }
      if (item.leukocyte != "") {
        NursingRecordData.nursingrecord_content +=
          "白血球 3054~9060:" + item.leukocyte + "; ";
      }
      if (item.erythrocyte != "") {
        NursingRecordData.nursingrecord_content +=
          "紅血球 ♂:4~5.52 ♀:3.78~4.99:" + item.erythrocyte + "; ";
      }
      if (item.hemoglobin != "") {
        NursingRecordData.nursingrecord_content +=
          "血色素 ♂:13.2~17.2 ♀:10.8~14.9:" + item.hemoglobin + "; ";
      }
      if (item.platelet != "") {
        NursingRecordData.nursingrecord_content +=
          "血小板 ♂:148~339 ♀:150~361:" + item.platelet + "; ";
      }
      if (item.albumin != "") {
        NursingRecordData.nursingrecord_content +=
          "白蛋白 3.5~5.7:" + item.albumin + "; ";
      }
      if (item.glycosylatedhemoglobin != "") {
        NursingRecordData.nursingrecord_content +=
          "醣化血色素 4.0-6.0:" + item.glycosylatedhemoglobin + "; ";
      }
      if (item.got != "") {
        NursingRecordData.nursingrecord_content +=
          "GOT(AST) 8 ~ 31:" + item.got + "; ";
      }
      if (item.gpt != "") {
        NursingRecordData.nursingrecord_content +=
          "GPT(ALT) 0 ~ 41:" + item.gpt + "; ";
      }
      if (item.bloodsugar != "") {
        NursingRecordData.nursingrecord_content +=
          "血糖 AC:70-100、PC:﹤140 :" + item.bloodsugar + "; ";
      }
      if (item.cholesterol != "") {
        NursingRecordData.nursingrecord_content +=
          "膽固醇 ﹤200 :" + item.cholesterol + "; ";
      }
      if (item.tg != "") {
        NursingRecordData.nursingrecord_content += "TG ﹤150:" + item.tg + "; ";
      }
      if (item.bun != "") {
        NursingRecordData.nursingrecord_content +=
          "BUN 7~25:" + item.bun + "; ";
      }
      if (item.cre != "") {
        NursingRecordData.nursingrecord_content +=
          "CRE 0.6~1.3:" + item.cre + "; ";
      }
      if (item.ua != "") {
        NursingRecordData.nursingrecord_content +=
          "UA ♂:4.4~7.6 ♀:2.3~6.6:" + item.ua + "; ";
      }
      if (item.na != "") {
        NursingRecordData.nursingrecord_content +=
          "Na 135~148:" + item.na + "; ";
      }
      if (item.k != "") {
        NursingRecordData.nursingrecord_content += "K 3.5~5.0:" + item.k + "; ";
      }
      if (item.hdlldl != "") {
        NursingRecordData.nursingrecord_content +=
          "HDL ﹥40 / LDL ﹤130 :" + item.hdlldl + "; ";
      }
      if (item.ph != "") {
        NursingRecordData.nursingrecord_content +=
          "尿液 PH 5~8:" + item.ph + "; ";
      }
      if (item.urineprotein != "") {
        NursingRecordData.nursingrecord_content +=
          "尿液 尿蛋白 (-):" + item.urineprotein + "; ";
      }
      if (item.ob != "") {
        NursingRecordData.nursingrecord_content +=
          "尿液 OB (-):" + item.ob + "; ";
      }
      if (item.rbc != "") {
        NursingRecordData.nursingrecord_content +=
          "尿液 RBC 0~5:" + item.rbc + "; ";
      }
      if (item.wbc != "") {
        NursingRecordData.nursingrecord_content +=
          "尿液 WBC 0~10:" + item.wbc + "; ";
      }
      if (item.bacteria != "") {
        NursingRecordData.nursingrecord_content +=
          "尿液 Bacteria (-):" + item.bacteria + "; ";
      }
      if (item.glu != "") {
        NursingRecordData.nursingrecord_content +=
          "尿液 GLU (-):" + item.glu + "; ";
      }
      if (item.urine != "") {
        NursingRecordData.nursingrecord_content +=
          "細菌培養 Urine:" + item.urine + "; ";
      }
      if (item.sputum != "") {
        NursingRecordData.nursingrecord_content +=
          "細菌培養 Sputum:" + item.sputum + "; ";
      }
      if (item.note != "") {
        NursingRecordData.nursingrecord_content +=
          "其他備註:" + item.note + "; ";
      }
      if (item.dispose != "") {
        NursingRecordData.nursingrecord_content +=
          "處置:" + item.dispose + "; ";
      }
      if (item.track != "") {
        NursingRecordData.nursingrecord_content += "追蹤:" + item.track + "; ";
      }

      //判斷護理紀錄資料是否存在
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey,
      };
      //取得該病歷所有護理紀錄資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" +
            this.$store.state.databaseName +
            "/nursingrecord",
          qs.stringify(postData)
        )
        .then((rs) => {
          var matchData = rs.data.filter((i) => {
            return i.date == item.date;
          });

          this.$store.commit("getTime"); //取得現在時間
          if (matchData.length == 0) {
            //新增
            var postData = {
              date: NursingRecordData.date,
              u_snkey: NursingRecordData.u_snkey,
              datalist: JSON.stringify(NursingRecordData),
              create_man:
                this.$store.state.pData.username +
                "(" +
                this.$store.state.time +
                ")", //執行動作的人,時間,內容
            };

            this.axios
              .post(
                "general/add/" +
                  this.$store.state.databaseName +
                  "/nursingrecord",
                qs.stringify(postData)
              )
              .then((rs) => {
                console.log(rs.data);
                if (rs.data.state == 1) {
                  var pop = {
                    msg: "轉入護理紀錄-已新增",
                    type: true,
                    theme: "success",
                  };
                  this.$store.commit("snackbar", pop);
                }
              });
          } else {
            //修改
            NursingRecordData["snkey"] = matchData[0].snkey;
            // NursingRecordData["nursingrecord_content"] =
            //   matchData[0].nursingrecord_content +
            //   NursingRecordData.nursingrecord_content;
            // NursingRecordData["edit_man"] =
            //   this.$store.state.pData.username +
            //   "(" +
            //   this.$store.state.time +
            //   ")"; //執行動作的人,時間,內容
            var datalist = JSON.parse(matchData[0].datalist);

            var postData = {
              snkey: matchData[0].snkey,
              datalist: JSON.stringify({
                nursingrecord_content:
                  datalist.nursingrecord_content +
                  NursingRecordData.nursingrecord_content,
              }),
              edit_man:
                this.$store.state.pData.username +
                "(" +
                this.$store.state.time +
                ")", //執行動作的人,時間,內容
            };

            this.axios
              .post(
                "general/edit/" +
                  this.$store.state.databaseName +
                  "/nursingrecord",
                qs.stringify(postData)
              )
              .then((rs) => {
                if (rs.data.state == 1) {
                  var pop = {
                    msg: "轉入護理紀錄資料-已追加",
                    type: true,
                    theme: "success",
                  };
                  this.$store.commit("snackbar", pop);
                }
              });
          }
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    print() {
      this.$refs.enterEdit.$emit("childFn", this.items);
    },
  },
};
</script>


<style lang="scss">
.inspectionreportlist {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1.2rem;
      font-family: "微軟正黑體";
      color: red;
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>