<template>
  <div class="inspectionreportadd">
    <v-dialog v-model="dialog">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="enterAdd">mdi-plus-circle</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleClass" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-form ref="form">
            <table class="table table-bordered mt-2">
              <thead class="form_title">
                <tr>
                  <th colspan="2" >項目</th>
                  <th>數值</th>
                </tr>
              </thead>
              <tbody>

                <tr>
                  <td class="text-left" colspan="2">日期</td> <td> <v-text-field v-model="list.date" type="date" hide-details :rules="emptyRules"></v-text-field> </td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">檢驗地點</td><td> <v-text-field v-model="list.location" hide-details></v-text-field> </td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">白血球 3054~9060</td> <td> <v-text-field v-model="list.leukocyte" hide-details></v-text-field> </td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">紅血球 ♂:4~5.52 ♀:3.78~4.99</td> <td> <v-text-field v-model="list.erythrocyte" hide-details></v-text-field> </td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">血色素 ♂:13.2~17.2 ♀:10.8~14.9</td> <td> <v-text-field v-model="list.hemoglobin" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">血小板 ♂:148~339 ♀:150~361</td> <td> <v-text-field v-model="list.platelet" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">白蛋白 3.5~5.7</td> <td> <v-text-field v-model="list.albumin" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">醣化血色素 4.0-6.0</td> <td> <v-text-field v-model="list.glycosylatedhemoglobin" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">GOT(AST) 8 ~ 31</td> <td> <v-text-field v-model="list.got" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">GPT(ALT) 0 ~ 41</td> <td> <v-text-field v-model="list.gpt" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">血糖 AC:70-100、PC:﹤140 </td> <td> <v-text-field v-model="list.bloodsugar" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">膽固醇 ﹤200 </td> <td> <v-text-field v-model="list.cholesterol" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">TG ﹤150</td> <td> <v-text-field v-model="list.tg" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">BUN 7~25</td> <td> <v-text-field v-model="list.bun" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">CRE 0.6~1.3</td> <td> <v-text-field v-model="list.cre" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">UA ♂:4.4~7.6 ♀:2.3~6.6</td> <td> <v-text-field v-model="list.ua" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">Na 135~148</td> <td> <v-text-field v-model="list.na" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">K 3.5~5.0</td> <td> <v-text-field v-model="list.k" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">HDL ﹥40 / LDL ﹤130 </td> <td> <v-text-field v-model="list.hdlldl" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td class="text-left align-middle" rowspan="7">尿液 </td> <td>PH 5~8</td> <td> <v-text-field v-model="list.ph" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td>尿蛋白 (-)</td> <td> <v-text-field v-model="list.urineprotein" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td>OB (-)</td> <td> <v-text-field v-model="list.ob" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td>RBC 0~5</td> <td> <v-text-field v-model="list.rbc" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td>WBC 0~10</td> <td> <v-text-field v-model="list.wbc" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td>Bacteria (-)</td> <td> <v-text-field v-model="list.bacteria" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td>GLU (-)</td> <td> <v-text-field v-model="list.glu" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td class="text-left align-middle" rowspan="2">細菌培養 </td> <td>Urine</td> <td> <v-text-field v-model="list.urine" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td>Sputum</td> <td><v-text-field v-model="list.sputum" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">其他備註 </td><td> <v-textarea v-model="list.note" hide-details></v-textarea></td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">處置 </td><td> <v-text-field v-model="list.dispose" hide-details></v-text-field></td>
                </tr>
                <tr>
                  <td class="text-left" colspan="2">追蹤 </td><td> <v-text-field v-model="list.track" hide-details></v-text-field></td>
                </tr>
                
              </tbody>
              
              
            </table>
            
          </v-form>

          
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="process=='add'" :loading="loading" :disabled="loading">確認新增</v-btn>
          <v-btn class="success" @click="editOK" v-if="process=='edit'" :loading="loading" :disabled="loading">確認修改</v-btn>
        </v-card-actions>

        <!-- <pre>{{list}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";

export default {
  data() {
    return {
      dialog: false,
      list: {}, //目前操作的表單內容
      oldList: {}, //存放修改前資料
      emptyRules: [v => !!v || "不可空白"],
      title: "", //抬頭
      titleClass: "", //抬頭顯示樣式
      process: "", //儲存執行動作
      loading: false,
    };
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("childFn", function(item) {
      this.dialog = true;
      this.enterEdit(item);
    });

  },

  methods: {
    enterAdd() {
      //執行新增流程
      this.process = "add";
      this.title = "新增檢驗報告單";
      this.titleClass = "font-weight-black error lighten-2";
      this.list = {
        date: dayjs().format("YYYY-MM-DD"),
      };
    },
    enterEdit(item) {
      //執行修改流程
      this.process = "edit";
      this.title = "修改使用管路";
      this.titleClass = "font-weight-black success lighten-2";
      this.oldList = item; //暫存未修改前資料
      this.list = item; //列表中複製一份顯示及修改用資料
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        var postData = {
          u_snkey: this.$store.state.uData.snkey,
          datalist: JSON.stringify(this.list),
          create_man:this.$store.state.pData.username + "(" + dayjs().format("YYYY-MM-DD HH:mm:ss") + ")" //執行動作的人,時間,內容
        };

        this.loading = true;
        this.axios
          .post(
            "general/add/" + this.$store.state.databaseName + "/inspectionreport",
            qs.stringify(postData)
          )
          .then(rs => {
            if (rs.data.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.list = {};
              this.dialog = false;
              this.$emit("getAllData");
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        var postData = {
          snkey:this.list.snkey,
          u_snkey: this.$store.state.uData.snkey,
          datalist: JSON.stringify(this.list),
          edit_man:this.$store.state.pData.username + "(" + dayjs().format("YYYY-MM-DD HH:mm:ss") + ")" //執行動作的人,時間,內容
        };

        this.loading = true;
        this.axios
          .post(
            "general/edit/" + this.$store.state.databaseName + "/inspectionreport",
            qs.stringify(postData)
          )
          .then(rs => {
            if (rs.data.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;

              for (var i in this.list) {
                if (this.oldList[i] == undefined) {
                  this.$set(this.oldList, i, "");
                }
                //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
                this.oldList[i] = this.list[i];
              }
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    },
    getFee() {
      //取得所有病歷資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      //取得所有資料
      this.axios
        .post("general/getAll/" + this.$store.state.databaseName + "/fee")
        .then(rs => {
          this.feeItems = rs.data.map(i => {
            return {
              snkey: i.snkey,
              text: i.name,
              price: i.price
            };
          });
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "node_modules/bootstrap/scss/bootstrap";
@import "node_modules/bootstrap-vue/src/index.scss";
.form_title {
  font-size: 1.2rem;
  text-align: center;
}
td {
  font-size: 1.2rem;
}
.vertical-mode {
  writing-mode: tb-rl;
  -webkit-writing-mode: vertical-rl;
  writing-mode: vertical-rl;
}
</style>